












import { Component, Prop, Vue } from 'vue-property-decorator'

@Component({
  name: 'BackToTop'
})
export default class extends Vue {
  @Prop({ default: 100 }) private visibilityHeight!: number // 按钮是否显示高度
  @Prop({ default: 'fade' }) private transitionName!: string  // 显示动画
  @Prop({ default: 0 }) private backPosition!: number  // 距离顶部的位置

  private visible = false  // 是否可见
  private isMoving = false   // 开始回顶部开关，方便可控
  private interval?: number  // 定时器

  mounted() {
    window.addEventListener('scroll', this.handleScroll,true)
  }

  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll)
    if (this.interval) {
      clearInterval(this.interval)
    }
  }

  private handleScroll() {
    this.visible = window.pageYOffset > this.visibilityHeight
  }

  private backToTop() {
    if (this.isMoving) return
    const start = window.pageYOffset
    let i = 0
    this.isMoving = true
    const interval = setInterval(() => {
      const next = Math.floor(this.easeInOutQuad(10 * i, start, -start, 500))
      if (next <= this.backPosition) {
        window.scrollTo(0, this.backPosition)
        clearInterval(interval)
        this.isMoving = false
      } else {
        window.scrollTo(0, next)
      }
      i++
    }, 16.7)
  }

  private easeInOutQuad(t: number, b: number, c: number, d: number) {
    if ((t /= d / 2) < 1) return (c / 2) * t * t + b
    return (-c / 2) * (--t * (t - 2) - 1) + b
  }
}
